import React, { useEffect, useState } from "react";
import backendURL from "../api";
import AddProductsModal from "../components/modals/AddProductsModal";
import EditProductsModal from "../components/modals/EditProductsModal";
import { Loading, Notification } from "../components/loading-notification";
import { SearchBar, CategoryFilter } from "../components/searchbar/SearchBar";
import { GetProductImage } from "../components/modals/GetProductImage";
import "./Products.scss";

const Products = () => {
	const [loading, setLoading] = useState(true);
	const [error, setError] = useState(null);
	const [success, setSuccess] = useState(null);
	const [products, setProducts] = useState();
	const [selectedProducts, setSelectedProducts] = useState([]);
	const [selectedProduct, setSelectedProduct] = useState(null);
	const [showAddModal, setshowAddModal] = useState(false);
	const [showEditModal, setshowEditModal] = useState(false);
	const [searchTerm, setSearchTerm] = useState("");
	const [categoryFilter, setCategoryFilter] = useState("");
	const [categories, setCategories] = useState([]);

	useEffect(() => {
		getProducts();
		getCategories();
	}, []);

	useEffect(() => {
		if (searchTerm) {
			searchProducts();
		} else {
			getProducts();
		}
	}, [searchTerm]);

	useEffect(() => {
		if (categoryFilter) {
			getProducts(null, null, null, {
				field: "categories",
				operator: "",
				value: categoryFilter,
			});
		} else {
			getProducts();
		}
	}, [categoryFilter]);

	const getCategories = async () => {
		setLoading(true);

		const response = await fetch(`${backendURL}/api/v1/wine/categories`, {
			method: "GET",
			headers: {
				"Content-Type": "application/json",
			},
		});

		const categoryData = await response.json();

		if (categoryData) {
			setCategories(categoryData.data.categories);
			setLoading(false);
		} else {
			setError(categoryData.message);
			setLoading(false);
		}
	};

	const searchProducts = async () => {
		setLoading(true);

		// Get jwt Bear token from local storage
		const token = localStorage.getItem("jwt");

		const response = await fetch(
			`${backendURL}/api/v1/wine/search?search=${searchTerm}`,
			{
				method: "GET",
				headers: {
					"Content-Type": "application/json",
					Authorization: `Bearer ${token}`,
				},
			}
		);

		const responseJson = await response.json();

		if (!response.ok) {
			setError(responseJson.message);
		}

		if (responseJson.status === "fail") {
			setError(responseJson.message);
		}

		setProducts(responseJson.data.wineProducts);
		setLoading(false);
		setError(null);
	};

	const getProducts = async (limit, page, sort, filter) => {
		setLoading(true);

		let url = `${backendURL}/api/v1/wine/`;

		if (limit) {
			url += `?limit=${limit}`;
		}
		if (page) {
			url += `&page=${page}`;
		}
		if (sort) {
			url += `&sort=${sort}`;
		}
		if (filter) {
			url += `?${filter.field}=${filter.operator}${filter.value}`;
		}

		const response = await fetch(url, {
			method: "GET",
			headers: {
				"Content-Type": "application/json",
			},
		});
		const productData = await response.json();

		if (productData) {
			setProducts(productData.data.wineProducts);
			setSuccess(productData.message);
			setLoading(false);
		} else {
			setError(productData.message);
			setLoading(false);
		}
	};

	const deleteProduct = async (productId) => {
		setLoading(true);

		// Get jwt Bear token from local storage
		const token = localStorage.getItem("jwt");

		const response = await fetch(`${backendURL}/api/v1/wine/${productId}`, {
			method: "DELETE",
			headers: {
				"Content-Type": "application/json",
				Authorization: `Bearer ${token}`,
			},
		});

		const productData = await response;

		if (productData.status === 204) {
			setSuccess(productData.message);
			setLoading(false);
		} else {
			setError(productData.message);
			setLoading(false);
		}
	};

	const handleShowAddModal = () => {
		getProducts();
		setshowAddModal(true);
	};

	const handleShowEditModal = (productId) => {
		setSelectedProduct(productId);
		setshowEditModal(true);
	};

	const handleSelect = (productId) => {
		if (selectedProducts.includes(productId)) {
			setSelectedProducts(selectedProducts.filter((id) => id !== productId));
		} else {
			setSelectedProducts([...selectedProducts, productId]);
		}
	};

	const handleDelete = () => {
		setProducts(
			products.filter((product) => !selectedProducts.includes(product._id))
		);

		selectedProducts.forEach((product) => {
			deleteProduct(product);
		});

		setSelectedProducts([]);
	};

	const handleSearch = (term) => {
		setSearchTerm(term);
	};

	const handleCategoryFilter = (category) => {
		setCategoryFilter(category);
	};

	return (
		<div className="product-container">
			{loading && <Loading />}
			{error && (
				<Notification
					message={error}
					severity="error"
					onClose={() => setError(null)}
				/>
			)}
			{success && (
				<Notification
					message={success}
					severity="success"
					onClose={() => setSuccess(null)}
				/>
			)}
			{showAddModal && (
				<AddProductsModal
					isOpen={showAddModal}
					onClose={() => setshowAddModal(false)}
				/>
			)}
			{showEditModal && (
				<EditProductsModal
					isOpen={showEditModal}
					onClose={() => setshowEditModal(false)}
					Product={products.find((product) => product._id === selectedProduct)}
				/>
			)}
			<h1>Products</h1>
			<div className="product-buttons">
				<button onClick={handleShowAddModal}>Add Product</button>
				<SearchBar onSearch={handleSearch} />
				<CategoryFilter
					categories={categories}
					onFilter={handleCategoryFilter}
				/>
				{selectedProducts.length > 0 && (
					<button onClick={handleDelete} className="delete-selected">
						Delete selected
					</button>
				)}
			</div>
			<table>
				<thead>
					<tr>
						<th></th>
						<th>Name</th>
						<th>Price</th>
						<th>Category</th>
						<th>Quantity</th>
						<th>Image</th>
						<th></th>
					</tr>
				</thead>
				<tbody>
					{products &&
						products.map((product) => (
							<tr key={product._id}>
								<td>
									<input
										type="checkbox"
										checked={selectedProducts.includes(product._id)}
										onChange={() => handleSelect(product._id)}
									/>
								</td>
								<td>{product.name}</td>
								<td>{product.price}</td>
								<td>{product.categories}</td>
								<td>{product.quantity}</td>
								<td>
									{product.image && (
										<div className="image-container">
											<img
												src={GetProductImage(product.image)}
												alt={product.name}
											/>
										</div>
									)}
								</td>
								<td>
									<button onClick={() => handleShowEditModal(product._id)}>
										Edit
									</button>
								</td>
							</tr>
						))}
				</tbody>
			</table>
		</div>
	);
};

export default Products;
