import React, { useState, useEffect } from "react";
import backendURL from "../api";
import "./Orders.scss";

const Orders = () => {
	const [orders, setOrders] = useState([]);

	useEffect(() => {
		getOrders();
	}, []);

	const getOrders = async () => {
		const res = await fetch(`${backendURL}/api/v1/orders/getAllOrders`, {
			method: "GET",
			headers: {
				"Content-Type": "application/json",
				Authorization: `Bearer ${localStorage.getItem("jwt")}`,
			},
		});

		const data = await res.json();

		if (data.status === "success") {
			setOrders(data.data.orders);
		}
	};

	const getDate = (date) => {
		const newDate = new Date(date);
		const day = newDate.getDate();
		const month = newDate.getMonth() + 1;
		const year = newDate.getFullYear();
		const fullDate = `${day}/${month}/${year}`;
		return fullDate;
	};

	const handleDeliver = async (orderId, userId) => {
		try {
			const res = await fetch(
				`${backendURL}/api/v1/orders//updateOrderStatus/${orderId}`,
				{
					method: "PATCH",
					headers: {
						"Content-Type": "application/json",
						Authorization: `Bearer ${localStorage.getItem("jwt")}`,
					},
					body: JSON.stringify({
						orderId,
						userId,
						orderStatus: "delivered",
					}),
				}
			);

			const data = await res.json();

			if (data.status === "success") {
				getOrders();
			}
		} catch (error) {
			console.error(error);
		}
	};

	return (
		<div className="order-container">
			<h1>Orders</h1>
			<div className="order-buttons"></div>
			<table>
				<thead>
					<tr>
						<th>Name</th>
						<th>Status</th>
						<th>Address</th>
						<th>Items</th>
						<th>Date</th>
						<th>Subtotal</th>
						<th>Shipping</th>
						<th>Total</th>
						<th>Action</th>
					</tr>
				</thead>
				<tbody>
					{orders &&
						orders.map((order) => (
							<tr key={order.orderId}>
								<td>{order.contact.address.name}</td>
								<td>{order.orderStatus}</td>
								<td>{`${order.contact.address.address}, ${order.contact.address.city}, ${order.contact.address.state}, ${order.contact.address.country}`}</td>
								<td>
									{order.items.map((item) => {
										return (
											<div>
												<p>{item.name}</p>
												<p>{item.quantity}</p>
											</div>
										);
									})}
								</td>
								<td>{getDate(order.date)}</td>
								<td>{order.subtotal}</td>
								<td>{order.deliveryFee}</td>
								<td>{order.total}</td>
								<td>
									{order.orderStatus === "paid" && (
										<button
											onClick={() => handleDeliver(order.orderId, order.userId)}
										>
											Deliver
										</button>
									)}
								</td>
							</tr>
						))}
				</tbody>
			</table>
		</div>
	);
};

export default Orders;
