import React, { useState } from "react";
import propTypes from "prop-types";
import { Snackbar } from "@material-ui/core";
import { Loading, Notification } from "../loading-notification";
import { Alert } from "@material-ui/lab";
import axios from "axios";
import backendURL from "../../api";
import Modal from "react-modal";
import "./ProductsModal.scss";

const EditProductsModal = ({ isOpen, onClose, Product }) => {
	const [loading, setLoading] = useState(false);
	const [error, setError] = useState(null);
	const [success, setSuccess] = useState(null);
	const [showImageSaved, setShowImageSaved] = useState(false);
	const [productData, setProductData] = useState({
		id: Product.id,
		name: Product.name,
		price: Product.price,
		summary: Product.summary,
		description: Product.description,
		quantity: Product.quantity,
		category: Product.categories,
		image: Product.image,
	});

	const editProduct = async (productData) => {
		setLoading(true);

		const response = await axios.patch(
			`${backendURL}/api/v1/wine/${productData.id}`,
			productData,
			{
				headers: {
					Authorization: `Bearer ${localStorage.getItem("jwt")}`,
					"Content-Type": "multipart/form-data",
				},
			}
		);

		const newProduct = await response;

		if (newProduct.status === 200 || newProduct.status === "success") {
			setProductData(newProduct.data.wineProduct);
			setSuccess("Product updated successfully");
			setLoading(false);
			onClose();
		} else {
			setError(newProduct.message);
			setLoading(false);
		}
	};

	const handleInputChange = (e) => {
		e.preventDefault();
		if (e.target.name === "image") {
			setProductData({ ...productData, image: e.target.files[0] });
		} else {
			setProductData({ ...productData, [e.target.name]: e.target.value });
		}
	};

	const handleSubmit = (e) => {
		e.preventDefault();

		editProduct(productData);

		// Save the image here
		setShowImageSaved(true);
	};

	const handleImageSavedClose = () => {
		setShowImageSaved(false);
	};

	return (
		<Modal
			isOpen={isOpen}
			onRequestClose={onClose}
			style={{
				overlay: {
					position: "fixed",
					top: 0,
					backgroundColor: "rgba(0, 0, 0, 0.5)",
					zIndex: 1000,
				},
				content: {
					top: "50%",
					left: "50%",
					right: "auto",
					bottom: "auto",
					marginRight: "-50%",
					transform: "translate(-50%, -50%)",
					background: "transparent",
					border: "none",
					zIndex: 5,
					padding: 0,
				},
			}}
		>
			<div className="modal-content">
				<div className="modal-header">
					<h5>Edit Product</h5>
				</div>
				<div className="modal-body">
					{productData && (
						<form onSubmit={handleSubmit}>
							<div>
								<label htmlFor="name">
									Name
									<input
										type="text"
										name="name"
										id="name"
										value={productData.name}
										onChange={handleInputChange}
									/>
								</label>
							</div>

							<div>
								<label htmlFor="price">
									Price
									<input
										type="number"
										name="price"
										id="price"
										value={productData.price}
										onChange={handleInputChange}
									/>
								</label>

								<div className="upload-container">
									<label htmlFor="image">Upload Image:</label>
									<input
										type="file"
										id="image"
										name="image"
										accept="image/*"
										onChange={handleInputChange}
									/>
									<label htmlFor="image" className="upload-button">
										Choose Image
									</label>
								</div>
							</div>

							<div>
								<label htmlFor="summary">
									Summary
									<textarea
										name="summary"
										id="summary"
										rows="3"
										value={productData.summary}
										onChange={handleInputChange}
									></textarea>
								</label>

								<label htmlFor="description">
									Description
									<textarea
										name="description"
										id="description"
										rows="6"
										value={productData.description}
										onChange={handleInputChange}
									></textarea>
								</label>
							</div>

							<div>
								<label htmlFor="quantity">
									Quantity
									<input
										type="number"
										name="quantity"
										id="quantity"
										value={productData.quantity}
										onChange={handleInputChange}
									/>
								</label>

								<label htmlFor="category">
									Category
									<input
										type="text"
										name="category"
										id="category"
										value={productData.category}
										onChange={handleInputChange}
									/>
								</label>
							</div>

							<div className="modal-buttons">
								<button type="submit" className="add-button">
									SAVE
								</button>
								<button
									type="button"
									className="cancel-button"
									onClick={onClose}
								>
									Cancel
								</button>
							</div>
						</form>
					)}
				</div>

				<Snackbar
					open={showImageSaved}
					autoHideDuration={3000}
					onClose={handleImageSavedClose}
				>
					<Alert severity="success" onClose={handleImageSavedClose}>
						Image saved!
					</Alert>
				</Snackbar>

				{loading && <Loading />}
				{error && (
					<Notification
						message={error}
						severity="error"
						onClose={() => setError(null)}
					/>
				)}
				{success && (
					<Notification
						message={success}
						severity="success"
						onClose={() => setSuccess(null)}
					/>
				)}
			</div>
		</Modal>
	);
};

EditProductsModal.propTypes = {
	isOpen: propTypes.bool.isRequired,
	onClose: propTypes.func.isRequired,
	Product: propTypes.object.isRequired,
};

export default EditProductsModal;
